.cart {
    position: relative;
}

.number-of-items {
    background-color: #ff5a00;
    color: #fff;
    font-weight: bold;
    border-radius: 50%;
    font-size: 1.1rem;
    padding: 0.2rem 0.5rem;
    position: absolute;
    bottom: 2rem;
    right: -0.2rem;
}

.bump {
    animation: bump 300ms ease-out;
}

@keyframes bump {
    0% {
        transform: scale(1);
    }
    10% {
        transform: scale(0.9);
    }
    30% {
        transform: scale(1.1);
    }
    50% {
        transform: scale(1.15);
    }
    100% {
        transform: scale(1);
    }
}